.el-message {
  z-index: 300;
}

.ReactVirtualized__List {
  outline: none !important;
  border: 1px solid #ddd;
  padding: 0 10px;
  overflow: auto !important;
}

.ReactVirtualized__List > div {
  /* Workaround for making table scrollable. */
  overflow: initial !important;
}

.dialog {
  width: 30%;
  min-width: 400px;
}

* {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  font-family: 'PingFang SC', 'Hiragino Sans GB', 'Microsoft JhengHei',
    'Microsoft YaHei', 'ヒラギノ角ゴ Pro W3', 'Hiragino Kaku Gothic ProN',
    'ヒラギノ角ゴ ProN', 'Hiragino Sans', Meiryo, 'メイリオ', Helvetica, Arial,
    Sans-Serif;

  -webkit-overflow-scrolling: touch;
  touch-action: manipulation;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  margin: 0;
  -webkit-backface-visibility: hidden;
  -webkit-overflow-scrolling: touch;

  color: #2c3e50;
}

a {
  color: #2c3e50;
}

@-webkit-keyframes loading-dot {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
    background: #fff;
  }

  50% {
    -webkit-transform: scale(1);
    transform: scale(1);
    background: #fff;
  }

  to {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
    background: #fff;
  }
}

@keyframes loading-dot {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
    background: #fff;
  }

  50% {
    -webkit-transform: scale(1);
    transform: scale(1);
    background: #fff;
  }

  to {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
    background: #fff;
  }
}

/* element override */

.main .el-select-dropdown__item.selected,
.main .el-select-dropdown__item.selected.hover {
  background-color: #71b38f;
}

.el-button.el-button--primary,
.el-button.el-button--primary:focus,
.el-button.el-button--primary:hover {
  color: #fff;
  background-color: #71b38f;
  border-color: #47965b;
}

.main button.el-button:focus,
.main button.el-button:hover {
  color: #47965b;
  border-color: #47965b !important;
}

.main .el-dropdown-menu {
  border-radius: 4px;
}
